@import url('https://fonts.googleapis.com/css2?family=Kaisei+Decol&display=swap');

.navbar {
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    height: 140px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: relative;
    font-family: 'Kaisei Decol', serif;
  }

 
  .navbar-logo {
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    color: #C39108;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
  }
  
  .fa-firstdraft {
    margin-left: 0.5rem;
    font-size: 1.6rem;
  }
  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 2rem;
  }
  
  .nav-item {
    display: flex;
    align-items: center;
    height: 140px;
  }
  
  .nav-links {
    color: #C39108;
    text-decoration: none;
    padding: 0.5rem 1rem;
  }
  
  .nav-links:hover {
    background-color: #f8e3aa;
    border-radius: 4px;
    transition: all 0.2s ease-out;
  }
  
  .fa-bars {
    color: #C39108;
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }
  
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      position: absolute;
      top: 120px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background: #242222;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
      background-color: #f8e3aa;
      border-radius: 0;
    }
  
    .navbar-logo {
      position: absolute;
      top: 50%;
      left: 10%;
      transform: translate(0%, -50%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 50%;
      right: 10%;
      transform: translate(0%, -50%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  .nav-item
  {
    height: auto;
  }
    .fa-times {
      color: #C39108;
      font-size: 2rem;
    }
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      padding: 1.5rem;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      background-color: #f8e3aa;
      text-decoration: none;
      color: #C39108;
      font-size: 1.5rem;
    }
  
    .nav-links-mobile:hover {
      background: #fff;
       background-color: #f8e3aa;
      transition: 250ms;
    }
  
    button {
      display: none;
    }
  }